@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}
body {
  background: #1e1f26;
  height: 100vh;
}
.App {
  background: white;
  width: 8.5in;
  min-height: 11in;
  height: 100%;
  margin: auto;
  padding-top: 20px;
}
.SectionLabels {
  margin-top: 10px;
  margin-left: 30px;
  color: rgba(32, 32, 32, 0.753);
}
.SectionLabelsSkill {
  color: rgba(32, 32, 32, 0.753);
  margin-left: -5px;
}
hr {
  margin: 0 auto;
  width: 95%;
}
.EditIcon,
.addIcon {
  height: 15px;
  width: 15px;
  cursor: pointer;
}
.addIcon:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  -webkit-filter: invert(37%) sepia(94%) saturate(1462%) hue-rotate(166deg)
    brightness(101%) contrast(101%);
          filter: invert(37%) sepia(94%) saturate(1462%) hue-rotate(166deg)
    brightness(101%) contrast(101%);
}
textarea {
  width: 500px;
  height: 100px;
}
.Export {
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  border: none;
  background: #404040;
  color: #ffffff !important;
  font-weight: 100;
  padding: 5px;
  text-transform: uppercase;
  border-radius: 6px;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}
.Export:hover {
  color: #404040 !important;
  font-weight: 500 !important;
  letter-spacing: 1px;
  background: #dbdbdb;
  box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  transition: all 0.3s ease 0s;
}
.ToHide {
  visibility: visible;
}

